import React, { Component } from 'react';
import { View, Text, StyleSheet, Button, TouchableHighlight, TouchableOpacity } from 'react-native';
import Colors from '../constants/colors';
import DogImage from './DogImage';

class PlayContent extends Component {
    constructor() {
        super();

        this.state = {
            imageURI: this.getURI,
            newImageCount: 1,
            windowHeight: window.innerHeight
        }
    }

    returnToHome = () => {
        this.props.parentCallback(true);
    }

    fetchNewData() {
        this.refs.dogimage.fetchData();
    }

    render() {
        return (
            <>
                <DogImage ref='dogimage' changeImage={this.state} dogBreed={this.props.dogBreed} overlay={false} />
                <View style={styles.playButtons}>
                    <View style={styles.playButton}>
                        <Button title='another one, please!' color={Colors.SMOKY_TOPAZ}
                            onPress={() => {
                                this.fetchNewData();
                            }} />
                    </View>
                    <View style={styles.backButton}>
                        <TouchableOpacity
                            onPress={() => {
                                this.returnToHome();
                            }}><Text>Go back</Text></TouchableOpacity>
                    </View>
                </View>
            </>
        )
    }
}

const styles = StyleSheet.create({
    playButtons: {
        justifyContent: 'space-evenly',
        alignContent: 'center',
        marginTop: 12,
        marginLeft: 7,
        marginRight: 7,
        height: 60,
        flexDirection: 'row',
        flexWrap: 'wrap',
        marginBottom: 12,
    },
    playButton: {
        backgroundColor: Colors.SMOKY_TOPAZ,
        justifyContent: 'center',
        alignItems: 'center',
        borderRadius: 10,
        width: '45%',
        height: '100%',
    },
    backButton: {
        alignItems: 'center',
        backgroundColor: Colors.ALABASTER,
        borderRadius: 10,
        color: Colors.RAISIN_BLACK,
        fontFamily: '-apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif',
        fontSize: 14,
        fontWeight: 500,
        height: '100%',
        justifyContent: 'center',
        textTransform: 'uppercase',
        width: '45%',
    },
})

export default PlayContent;